// src/details/File4.js
import { useState } from 'react';
import { Tab, Tabs } from 'react-bootstrap';

const File4 = () => {
    const [key, setKey] = useState('detail');

    return (
        <div>
            <h2>Gene Network</h2>
            <hr />
            <h4>File information</h4>
            <Tabs
                defaultActiveKey="detail"
                id="file-info"
                className="mb-3"
                activeKey={key}
                onSelect={(k) => setKey(k)}
                justify
                >
                <Tab eventKey="detail" title="Detail">
                    Detail of this file
                </Tab>
                <Tab eventKey="compact" title="Compact">
                    Compact detail of this file
                </Tab>
                <Tab eventKey="column" title="Column">
                    Column values of the file
                </Tab>
            </Tabs>

        </div>
    );
};

export default File4;
